.progressBarContainer {
  text-align: center;
  position: relative;
  margin-top: 30px;
}

/*progressbar*/
.progressBar {
  padding: 20px;
  overflow: hidden;
  /*CSS counters to number the steps*/
  counter-reset: step;
}

.progressBar li {
  list-style-type: none;
  color: black;
  text-transform: uppercase;
  font-size: 12px;
  width: 25%;
  float: left;
  text-align: center;
  position: relative;
  letter-spacing: 1px;
}

.progressBar li:before {
  content: counter(step);
  counter-increment: step;
  width: 24px;
  height: 24px;
  line-height: 26px;
  display: block;
  font-size: 12px;
  color: white;
  background: black;
  border-radius: 25px;
  margin: 0 auto 10px auto;
}

/*progressBar connectors*/
.progressBar li:after {
  content: "";
  width: 100%;
  height: 2px;
  background: black;
  position: absolute;
  left: -50%;
  top: 9px;
  z-index: -1; /*put it behind the numbers*/
}

.progressBar li:first-child:after {
  /*connector not needed before the first step*/
  content: none;
}

/*marking active/completed steps green*/
.progressBar li.active:before,
.progressBar li.active:after {
  background: green;
  color: white;
}

